.snip1584 {
	font-family: 'Monospace', sans-serif;
	position: relative;
	display: inline-block;
	overflow: hidden;
	margin: 2px !important;
	min-width: 230px;
	max-width: 315px;
	width: 100%;
	color: #ffffff;
	font-size: 16px;
	text-align: left;
}
.snip1584 * {
	box-sizing: border-box;
	transition: all 0.25s ease;
}
.snip1584:before {
	position: absolute;
	top: 10px;
	bottom: 10px;
	left: 10px;
	right: 10px;
	top: 100%;
	content: '';
	background-color: rgba(51, 51, 51, 0.9);
	transition: all 0.25s ease;
	transition-delay: 0.25s;
}
.snip1584 img {
	vertical-align: top;
	width: 100%;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}
.snip1584 figcaption {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 1;
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
}
.snip1584 h3,
.snip1584 h5 {
	margin: 0;
	opacity: 0;
	letter-spacing: 1px;
}
.snip1584 h3 {
	-webkit-transform: translateY(-100%);
	transform: translateY(-100%);
	text-transform: uppercase;
	font-weight: 400;
	transition-delay: 0.05s;
	margin-bottom: 5px;
}
.snip1584 h5 {
	font-weight: normal;
	background-color: #ae895d;
	padding: 3px 10px;
	-webkit-transform: translateY(-100%);
	transform: translateY(-100%);
	transition-delay: 0s;
}
.snip1584 a {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 1;
}

.snip1584:hover:before,
.snip1584.hover:before {
	top: 10px;
	transition-delay: 0s;
}
.snip1584:hover h3,
.snip1584.hover h3,
.snip1584:hover h5,
.snip1584.hover h5 {
	-webkit-transform: translateY(0);
	transform: translateY(0);
	opacity: 1;
}

.snip1584:hover h3,
.snip1584.hover h3 {
	transition-delay: 0.3s;
}
.snip1584:hover h5,
.snip1584.hover h5 {
	transition-delay: 0.2s;
}
